.heading {
  color: darkblue;
  text-align: center;
}

.list {
  list-style-type: none;
  padding: 0;
}

.list-item {
  background: lightgray;
  padding: 5px;
  margin: 5px;
  border-radius: 5px;
}

.result {
  font-weight: bold;
  color: green;
}

.button {
  padding: 8px 12px;
  margin: 5px;
  border: none;
  background: blue;
  color: white;
  cursor: pointer;
}

.greeting {
  font-size: 24px;
  font-weight: bold;
}

.converter {
  text-align: center;
}

.input {
  padding: 5px;
  margin: 5px;
}